import React, { Component } from 'react'
import ReactDOM  from 'react-dom';


export default class Welcome extends Component {
 
funDom(){
var container= document.getElementById("myID");
ReactDOM.findDOMNode(container).src="https://www.imgacademy.com/sites/default/files/2013-slide1.jpg"


}

  render() {
    return (
      <div>
       
        <button onClick={this.funDom}>Dom</button>
        <img id="myID" src="https://www.imgacademy.com/themes/custom/imgacademy/images/helpbox-contact.jpg"></img>
      </div>
    )
  }
}
