import React, { Component } from 'react'

export default class Form extends Component {

    constructor(){

        super()
        this.state={
            name:" ",
        }

    }


    onchangeHandeler=(event)=>{
        var getInput = event.target.value
        this.setState({name:getInput})
        
    }


    render() {
        return (
            <div>
                <p>this is handeler text change</p>
                <h1>{this.state.name}</h1>
                <input onChange={this.onchangeHandeler} type="text" placeholder="your Name"></input><br></br>
                <input type="submit" value="Submit Now"></input>
            </div>
        )
    }
}
